
const Noticias = (props) => {
    
    const {urlToImage, url, title, description, source} = props.noticias;


    let imagen = '';

    if(urlToImage !== '') {
        imagen = <img src={urlToImage} alt={title} className="card-img-top" />
    }
    return (
        <div className="row">
            {props.noticias.map(noticia => (
                <div className="col-md-6 col-12 mb-4">
                    <div className="card">
                        <div className="card-image" rel={key}>
                            {imagen}
                        </div>
                    </div>
                </div>
            ))}
        </div>
    );
}

export default Noticias;